import React from "react";
import TodoItem from "./TodoItem";

const TodoList = () => {
  const todos = [
    { id: 1, title: "We believe if" },
    { id: 2, title: "We believe it" }
  ];
  return todos.map((todo) => (
    <ul
      style={{
        listStyleType: "none",
        background: "#cdc",
        padding: "15px",
        height: "3vh"
      }}
    >
      <TodoItem id={todo.id} title={todo.title} />
    </ul>
  ));
};
export default TodoList;
